


.main-container{
    min-height: 80vh;
}

.central-form{
    /* max-width: 1100px; */
    max-width: var(--max-width);
    padding: 10px;
    display: flex;
    border-radius: 12px;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    background-color: #1f2235;

}

.central-form h2{
    color: white;
    letter-spacing: 0.2rem;
    margin-bottom: 20px;
}

.back-form{
    max-width: 380px;
    margin: 0 auto;
}

.back-form .img-back{
    width: 100%;
    margin-bottom: 20px;
}

.img-back h4{
    color: white;
    letter-spacing: 0.3rem;
    font-weight: 400;
    font-size: 18px;
    opacity: 0.5;
}

.back-form .img-back img{
    width: 100%;
    opacity: 0.5;
}

.central-form form{
    width:auto;
    display: flex;
    padding: 15px;
    justify-content: center;
    flex-direction: column;
    margin-top: -52px;
    background-color: white;
    border-radius: 12px;
}

form {
    text-align: center;
    margin-bottom: 10px;
    color: darkorange;
    letter-spacing:0.4rem;
    font-family: "Poppins Bold";
}

form label{
    margin-bottom: 5px;
    color: #111;
    letter-spacing: 0.2rem;
}

form input, textarea{
    width: 100%;
    border: 2px solid rgba(0, 0, 0, 0);
    outline: none;
    background-color: rgba(230, 230, 230, 0.6);
    padding: 0.5rem;
    margin-bottom: 20px;
    border-radius: 10px;
    transition: 0.3s
}

textarea:focus{
    border: 2px solid rgba(30, 85, 250, 0.47);
    background-color:white;
}

input:focus{
    border: 2px solid rgba(30, 85, 250, 0.47);
    background-color:white;
}

form button {
    margin-top: 15px;
    padding: 5px;
    outline: none;
    width: 160px;
    border: 2px solid darkorange;
    font-size: 18px;
    border-radius: 19px;
    color: white;
    background-color:#1f2235;
    align-items: center;
    justify-content: center;
}

form button:hover{
    background-color: #1f2235;
    transition: all ease 1s;
    border: 2px solid #1f2235;
}

.img-back {
    flex: 1;
    align-self: center;
    margin-right: 20px;
}

form{
    flex: 0.9;
}

.col i{
    padding: 7px;
    color: white;
}

.col i:hover{
    transform: scale(1.5);
}

.col h2{
    display: flex;
}

.col .icons{
    display: flex;
}

@media screen and (min-width: 940px) {
    .back-form{
        max-width: 1100px;
        margin: auto;
        display: flex;
        justify-content: space-between;
        margin-top:30px;
    }

    .img-back{
        flex: 1;
        align-self: center;
        margin-right: 20px;
    }
    form {
        flex: 0.9;
    }
}

.load{
    width: 60px;
}


.scroll-home {
    background-color: tomato;
    display: inline;
}


.col > h2, img-back > h4 {
    font-size: 1rem;
    padding-bottom: 5px;
}

