  .filters-container h5 {
    font-weight: 500;
    margin: 1.5rem 0 0.5rem;
    font-size: 0.85rem;
  }
  
  .search-input {
    padding: 0.5rem;
    background: var(--grey-200);
    border-radius: var(--borderRadius);
    border-color: transparent;
    letter-spacing: var(--letterSpacing);
  }
  
  .company-btn {
    display: block;
    margin: 0.25em 0;
    padding: 0.25rem;
    text-transform: capitalize;
    background: transparent;
    border-color: transparent;
    letter-spacing: var(--letterSpacing);
    color: var(--grey-500);
    cursor: pointer;
    transition: var(--transition);
    font-size: 0.85rem;
  }
  .company-btn:hover {
    color: var(--grey-700);
  }
  .product {
    margin-bottom: 1rem;


    /* transform: translateX(-100px);
    -webkit-transform: translateX(-100px);
    -moz-transform: translateX(-100px);
    -ms-transform: translateX(-100px);
    -o-transform: translateX(-100px);

    transition-property: transform, opacity;
    transition-duration: 1s;
    transition-timing-function: ease; */

}
  .product-img {
    border-radius: var(--borderRadius);
    height: 15rem;
    /* max-width: 370px; */
  }
  .product footer {
    padding: 0.75rem 0;
    text-align: center;
  }
  .product-name {
    margin-bottom: 0.25rem;
    text-transform: capitalize;
    letter-spacing: var(--letterSpacing);
    color: var(--grey-700);
    padding-left: 10px;
  }
  .product-price {
    margin-bottom: 0;
    color: var(--grey-700);
    font-weight: 700;
    font-size: 1rem;
    letter-spacing: var(--letterSpacing);
  }

  
  @media screen and (min-width: 768px) {
    .portfolio {
      grid-template-columns: 200px 1fr;
    }
  }
  @media screen and (min-width: 992px) {
    .portfolio-container {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 1rem;
      /* overflow-x: hidden; */
    }
    .portfolio-container .product-img {
      /* height: 25rem; */
      height: 20rem;
      transition: transform .50s;
      border-radius: 10%;
      -webkit-border-radius: 10%;
      -moz-border-radius: 10%;
      -ms-border-radius: 10%;
      -o-border-radius: 10%;
      box-shadow: 5px 5px black;
      -webkit-transition: transform .50s;
      -moz-transition: transform .50s;
      -ms-transition: transform .50s;
      -o-transition: transform .50s;
}

    .product-img:hover {
      z-index: 1000;
      transform: scale(1.1);
      -webkit-transform: scale(1.1);
      -moz-transform: scale(1.1);
      -ms-transform: scale(1.1);
      -o-transform: scale(1.1);
}

    .portfolio-container .product-name {
      font-size: 0.85rem;
    }
    .portfolio-container .product-price {
      font-size: 0.85rem;
    }
    .product {
      margin-bottom: 0;
      position: relative;
      /* overflow-x: hidden; */
    }
  }
  @media screen and (min-width: 1170px) {
    .portfolio-container {
      grid-template-columns: repeat(3, 1fr);
    }
  }


  @media screen and (min-width: 1500px) {
    .portfolio-container {
      grid-template-columns: repeat(4, 1fr);
    }
  }
 
